.wrapper {
    padding:20px;
}
.drop_zone {
    display: flex;
    height: 200px;
    width: 100%;
    background: white;
    
    background-repeat: no-repeat;
    background-size: 100px;
    background-position: center;
    background-color: #ffffff;
    cursor: pointer;
    border: 2px dashed #ccc;
    border-radius: 6px;
    margin-bottom: 25px;
    background-position: center 28px;
}
.drop_zone>p {
    margin: auto;
}
.image{
   width: 200px;
   height: 200px;
   border-radius: 4px;
   display: inline-block;
}
.square:before{
    content: "";
    display: block;
    padding-top: 100%;  /* initial ratio of 1:1*/
}

.center{
    justify-content: center;
    align-items: center;
    display: flex;
}
#dropdown-basic-button{
    min-width: 100%;
}